
"use client";
import { useEffect, useState } from 'react';

export default function TimeEntriesTable() {
  const [entries, setEntries] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch('/api/time-entries')
      .then(res => res.json())
      .then(data => {
        setEntries(data);
        setLoading(false);
      });
  }, []);

  if (loading) return <div>Loading time entries...</div>;

  return (
    <table className="data-table">
      <thead>
        <tr>
          <th>User</th>
          <th>Account</th>
          <th>Task</th>
          <th>Start</th>
          <th>End</th>
          <th>Duration (min)</th>
          <th>Description</th>
        </tr>
      </thead>
      <tbody>
        {entries.map((e: any) => (
          <tr key={e.id}>
            <td>{e.userId}</td>
            <td>{e.accountId}</td>
            <td>{e.taskId}</td>
            <td>{new Date(e.startedAt).toLocaleString()}</td>
            <td>{new Date(e.endedAt).toLocaleString()}</td>
            <td>{e.durationMinutes}</td>
            <td>{e.description}</td>
          </tr>
        ))}
      </tbody>
    </table>
  );
}
